.js-list {
  transition-property: width;
  transition-duration: 0.4s;
  transition-timing-function: cubic-bezier(0.52, 0.01, 0.13, 1.01);
  transition-delay: var(--delay, 0);
}
.js-list .list-cards .list-card {
  transition-property: width;
  transition-duration: 0.4s;
  transition-timing-function: cubic-bezier(0.52, 0.01, 0.13, 1.01);
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content {
  transition-property: background-color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .js-list-header {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .list-card {
  transition-property: opacity;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
  transition-property: background-color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .list-card .js-card-name {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .list-card .js-badges {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .card-composer .js-composer {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
  transition-property: background-color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .card-composer .cc-controls {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}
.js-list .js-list-content .card-composer-container {
  transition-property: color;
  transition-duration: 0.4s;
  transition-delay: var(--delay, 0);
}

